.keycombo {
  white-space: pre;
}
.keycombo.combined {
  padding: 0 0 3px;
  border-radius: 4px;
  background: #111;
  border: solid 1px #555;
}
.keycombo kbd {
  font-family: Avenir, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
}
.keycombo kbd.symbol, .keycombo kbd.mod, .keycombo kbd.key {
  background: #191919;
  border-radius: 4px;
  border: solid 1px #444;
  color: #bbb;
  display: inline-block;
  font-size: 0.85em;
  line-height: 1.4;
  margin: 0;
  min-width: 2.4ch;
  padding: 0 2px;
  text-align: center;
}

a:hover {
  color: white;
}

code.highlighter-rouge {
  color: #bbb;
  background-color: #111;
  border: 1px solid #444;
}

.header-dark {
  background-color: #302e31e5 !important;
}

table {
  display: block;
  width: 100%;
  overflow: auto;
  word-break: normal;
  word-break: keep-all;
  -webkit-overflow-scrolling: touch;
  margin-bottom: 1em;
}
table th {
  font-weight: bold;
}
table th, table td {
  padding: 0.5rem 1rem;
  border: 1px solid #dee2e6;
}

#scrollButton {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  padding: 6px;
  border-radius: 50%;
}

#scrollButton:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

blockquote {
  display: block;
  border-width: 2px 0;
  border-style: solid;
  border-color: #eee;
  padding: 1.5em 0 0.5em;
  margin: 1.5em 0;
  position: relative;
}

blockquote:before {
  content: "";
  position: absolute;
  top: 0em;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  width: 3rem;
  height: 2rem;
  font-size: x-large;
  color: #555;
  text-align: center;
}

a.nav-link:hover {
  text-decoration: underline;
}

img {
  border-radius: 5px;
  max-width: 100%;
}

.img-align-right {
  float: right;
  margin-right: 1em;
  font-size: 11pt;
  text-align: center;
}

.center {
  display: block;
  height: auto;
  margin: auto;
  width: 70%;
}

